.app-container{
  text-align:center
}

::-webkit-scrollbar{
  width:0;
  background:transparent
}

::-webkit-scrollbar-thumb{
  background:#ffffff00
}

.search-bar{
  background-image:linear-gradient(to top, rgba(0,0,0,0), #000);
  width:100%;
  position:absolute;
  height:50px;
  z-index:1;
  transition:all 200ms ease-in
}

.search-bar .search-icon{
  width:25px;
  height:25px
}

.search-bar .back-icon{
  width:23px;
  height:23px
}

.search-bar .search-input{
  height:32px;
  border:1px solid #1f1f1f;
  border-radius:4px;
  background-color:transparent;
  max-width:350px;
  -webkit-animation:fadeIn 200ms;
  animation:fadeIn 200ms
}

.search-bar.show-search{
  background-color:#000000b3
}

@media only screen and (max-width: 640px){
  .search-bar .search-input{
    max-width:unset
  }

  .search-bar.show-search .header-title{
    display:none
  }
}

@-webkit-keyframes fadeIn{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

@keyframes fadeIn{
  from{
    opacity:0
  }

  to{
    opacity:1
  }
}

