/* purgecss start ignore */

.home-container{
  height:100vh;
  overflow:auto;
  padding-top:60px
}

.home-container .movie-card{
  background-color:#222222c7;
  position:relative;
  border-radius:4px
}

.home-container .movie-card:hover{
  transition:all ease-in 350ms;
  background-color:#353535
}

.home-container .movie-card:hover .play-button{
  opacity:1
}

.home-container .movie-card img{
  object-fit:contain
}

.home-container .movie-card .play-button{
  transition:all ease-in 450ms;
  opacity:0;
  width:30px;
  height:30px;
  background-color:#ffe300;
  border-radius:30px;
  position:absolute;
  bottom:30px;
  right:15px;
  display:flex;
  align-items:center;
  justify-content:center;
  box-shadow:2px 2px 3px 0px #252525;
  cursor:pointer
}

.home-container .movie-card .play-button .play{
  box-sizing:border-box;
  height:12px;
  border-color:transparent transparent transparent #fff;
  transition:100ms all ease;
  will-change:border-width;
  cursor:pointer;
  border-style:solid;
  border-width:6px 0 6px 8px
}

